/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
}

p {
  font-size: 2.3rem;
}

[class*=container],
.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

[class*=__heading] {
  color: #284b63;
}

img {
  max-width: 100%;
  display: block;
  pointer-events: none;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

.section {
  padding: 3rem 0;
}

input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

h1,
h2,
h3 {
  margin: 0;
  line-height: 1.5;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3.2rem;
}

[class$=heading] {
  margin-bottom: 2rem;
}

a {
  text-decoration: none;
}

.text-white {
  color: white;
}

.mt-3 {
  margin-top: 3rem;
}

.p-block-3 {
  padding: 3rem;
}

.pt-w-100 {
  padding-top: 100vh;
}

.text-center {
  text-align: center;
}

/** Media Queries **/
.hero {
  z-index: -5;
  position: absolute;
  height: 100vh;
  min-width: 20px;
  width: 100%;
  background-image: linear-gradient(rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.185) 100%), url("../../build/img/marvel-universe.webp");
  background-size: cover;
  background-position: top center;
  background-repeat: no-repeat;
}

.header {
  position: sticky;
  top: 0;
  background-color: rgba(0, 0, 0, 0.3);
}

#marvel-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  row-gap: 2.5rem;
  column-gap: 1.5rem;
}

@media (min-width: 480px) {
  #marvel-grid {
    grid-template-columns: repeat(5, 1fr);
  }
}
#marvel-grid div {
  background-image: linear-gradient(black 0%, rgba(0, 0, 0, 0.7) 100%);
  color: white;
  border-radius: 2rem;
  padding-top: 1rem;
  box-shadow: 5px 5px 2px rgba(0, 0, 0, 0.3);
}

#marvel-grid div h3 {
  font-size: 1.8rem;
}

#marvel-grid div img {
  margin: 0 auto;
}

#marvel-grid div p {
  font-weight: 700;
  padding-inline: 2rem;
  font-size: 1.5rem;
}

#marvel-grid div p span {
  font-size: 1.3rem;
}